body,
html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  background-color: #f6f6f6;
}

body {
  font-family: 'Roboto', sans-serif;
  font-size: 12px;
}


.skip {
  position: absolute;
  top: -45px;
  left: 0;
  background-color: #ffffff;
  color: #FF8C32;
  padding: 12px;
  z-index: 10;
  font-size: 16px;
  text-decoration: none;
}
 
.skip:focus {
   top: 0;
}

.heading {
  display: flex;
  justify-content: space-between;
  width: 100%;
  background-color:  #FF8C32;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.038);
}

.head-title {
  font-weight: bold;
  font-size: 28px;
  color: #ffff;
  
  padding: 15px 30px;
}
.head-menu{
  font-size: 32px;
  display: block;
  cursor: pointer;
  padding: 0 10px 0 10px;
  border: none;
  background: none;
  color: #ffff;
}

.nav {
  width: 40%;
  margin-top: 64px;
  text-align: center;
}

.nav-list {
  width: 50%;
  padding: 0;
  margin: 0;
}

.nav-item {
  display: block;
  font-size: 18px;
  padding: 0;
  width: 100%;
  border-bottom: 1px solid #e5e5e5;

}


.nav a {
  display: block;
  text-decoration: none;
  color: #545454;
  padding: 20px 20px;
}

.nav a:hover {
  text-decoration: underline;
}


.home {
  position: relative;
  text-align: center;

}
picture img{
    width: 100%;
    max-height: 300px;
    height: auto;
    object-fit: cover;
}
.home::before{
  content: "";
  height: auto;
  background-color: rgba(0, 0, 0, 0.547);
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
}
.home-inner {
  position: absolute;
  width: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  font-size: 32px;
  font-weight: bold;
  text-align: center;
  z-index: 1; 
}


.home-title {
  font-size: 18px;
  font-weight: bold;
  letter-spacing: 1px;
}

.home-ket {
  
  margin-top: 5px;
  font-size: 12px;
  font-weight: lighter;
}
.menu-label {
  max-width: 85%;
  margin: 50px auto;
  text-align: center;
}

.favor{
  max-width: 85%;
  margin: 50px auto;
  text-align: center;
}

.label-title {
  font-size: 20px;
  font-weight: lighter;

}

.label-title::after {
  content: '';
  margin-top: 16px;
  display: block;
}

.restaurant-card {
  width: 100%;
  margin-top: 10px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  overflow: hidden;
  background-color: #ffffff;
}

.restaurant-card h2 {
  color: #535353;
  margin-top: 10px;
  text-align: left;
  font-size: 20px;
}

.restaurant-card p {
  font-size: 14px;
}

.description {
  margin-top: 10px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 5;
  text-align: justify;
}

.restaurant-card img {
  width: 100%;
  max-height: 175px;
  overflow: hidden;
  object-fit: cover;
}


