    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  #reviewForm input[type="text"],
  #reviewForm textarea {
    width: 100%;
    padding: 12px;
    margin: 5px 0;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 16px;
  }
  
  #reviewForm textarea {
    resize: vertical;
    min-height: 100px;
  }
  
  #reviewForm button {
    background-color: #FF8C32;
    color: #fff;
    border: none;
    margin-top: 10px;
    border-radius: 5px;
    padding: 13px 20px;
    font-size: 16px;
    cursor: pointer;
  }
  
  #reviewForm button:hover {
    background-color: #f69f5c;
  }

.like {
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #FF8C32;;
  position: fixed;
  bottom: 16px;
  right: 16px;
  border-radius: 50%;
