@media screen and (max-width: 499px) {
  .nav {
    z-index: 15;
    background-color: #ffffffe9;
    width: 100%;
    position: absolute;
    -webkit-transform: translate(-500px, 0);
    transform: translate(-500px, 0);
    transition: transform 0.3s ease;
  }

  .open {
    -webkit-transform: translate(0, 0);
    transform: translate(0,0);
  }

  .nav-item {
    width: 200%;
    text-align: left;
  }

  

}

@media screen and (min-width: 499px) {


  .head-menu{
    display: none;
  }
  
  .nav{
    width: 50%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0;
  }
  .nav-list{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 18px;
  }

  .nav a{
    color: #ffffff;
    padding: 12px 0 12px 0;
  }

  .nav li {
    list-style: none;
    }

  .nav-item{
    border: none;
  }
  

  .restaurant{
    display: grid;
    grid-template-columns: 1fr;
    grid-row-gap: 16px;
  }

  .home{
    width: 100%;
    /* padding: 90px 0px; */
  }
  
  .home-inner {
    z-index: 10;
    margin: 0;
  }
  
  .home-title {
    color: #ffffff;
    font-weight: bold;
    font-size: 24px;
    letter-spacing: 1px;
  }
  
  .home-ket {
    color: #ffffff;
    margin-top: 7px;
    font-size: 16px;
    font-weight:lighter;
  }
 

}
 

@media screen and (min-width: 600px) {
  

  .nav{
    width: 50%;
  }

  .restaurant{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 14px;
    grid-row-gap: 16px;
  }

  .menu-item {
    padding: 16px 32px 32px 32px;
  }

  .menu-item-title {
    font-size: 18px;
  }

  .menu-item-description {
    font-size: 14px;
  }

  .favor{
    max-width: 85%;
    margin: 50px auto;
    margin-bottom: 155px;
    text-align: center;
  }
  
 
}

@media screen and (min-width: 700px) {
  .restaurant{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 14px;
    grid-row-gap: 16px;
  }


  .menu{
    width: 90%;
    place-items: center;
    margin: 0 auto;
  }

  .detail{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 30px;
  }
}

@media screen and (min-width: 800px) {
  .nav{
    width: 45%;
  }
  .nav-list {
    width: 100%;
    margin: 0 auto;
  }
  .home-title{
    text-align: center;
  }
  
}

@media screen and (min-width: 900px) {
  .nav{
    width: 40%;
  }
  .restaurant{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }
  
}

@media screen and (min-width: 1200px) {
  .nav{
    width: 35%;
  }
  
